.header {
  height: 80px;
  background-color: white;
}
.header .header-top {
  height: 40px;
  border-bottom: 1px solid #1DA57A;
  line-height: 40px;
  text-align: right;
}
.header .header-top a {
  margin: 0 20px;
}
.header .header-bottom {
  height: 40px;
  display: flex;
  line-height: 40px;
  padding-right: 20px;
}
.header .header-bottom .header-bottom-left {
  width: 25%;
  text-align: center;
  font-size: 20px;
}
.header .header-bottom .header-bottom-left:after {
  content: '';
  border-bottom: 20px rgba(0, 0, 0, 0) solid;
  border-top: 20px white solid;
  border-right: 20px rgba(0, 0, 0, 0) solid;
  border-left: 20px rgba(0, 0, 0, 0) solid;
}
.header .header-bottom .header-bottom-right {
  width: 75%;
  text-align: right;
}
.header .header-bottom .header-bottom-right img {
  width: 30px;
  margin: 0 10px;
}
