.login-wrap {
  width: 100%;
  height: 100%;
  background: url(/static/media/bg.ab45504b.jpg);
  background-size: 100% 100%;
}
.login-wrap .login-header {
  width: 100%;
  height: 80px;
  background: rgba(21, 20, 13, 0.5);
  display: flex;
}
.login-wrap .login-header img {
  width: 40px;
  height: 40px;
  align-self: center;
  margin: 0 20px 0 50px;
}
.login-wrap .login-header h1 {
  align-self: center;
  color: white;
  font-size: 25px;
}
.login-wrap .login-content {
  position: relative;
  width: 400px;
  height: 300px;
  background-color: white;
  margin: 100px auto;
  padding: 20px 30px;
}
.login-wrap .login-content h2 {
  font-size: 35px;
  font-weight: bold;
  text-align: center;
}
.login-wrap .login-content .error-msg {
  visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  text-align: center;
  height: 30px;
  width: 100%;
  background: #f60c1a;
  color: #ffffff;
  font-size: 16px;
  -webkit-transform: translateY(-30px);
          transform: translateY(-30px);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.login-wrap .login-content .error-msg.show {
  visibility: visible;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.login-wrap .login-content .login-form .login-form-button {
  width: 100%;
}
